'use strict';

// MODULES //

import dsumkbn from '@stdlib/blas-ext-base-dsumkbn';


// MAIN //

/**
* Computes the sum of double-precision floating-point strided array elements.
*
* @param {PositiveInteger} N - number of indexed elements
* @param {Float64Array} x - input array
* @param {integer} stride - stride length
* @returns {number} sum
*
* @example
* import Float64Array from '@stdlib/array-float64';
*
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
* var N = x.length;
*
* var v = dsum( N, x, 1 );
* // returns 1.0
*/
function dsum( N, x, stride ) {
	return dsumkbn( N, x, stride );
}


// EXPORTS //

export default dsum;
